import React, { useState } from 'react';
import { QrReader } from 'react-qr-reader';
import CryptoJS from 'crypto-js';

const QRCodeScanner = () => {
  const [decodedData, setDecodedData] = useState({ publicText: 'No result', privateText: 'No result' });
  const password = "MySecretPassword4ReactQR";

  const handleScan = (result) => {
    if (result) {
      try {
        // Split the result into public text and encrypted private text
        const [publicText, encryptedPrivateText] = result.text.split('||');
        // Decrypt the private text
        const bytes = CryptoJS.AES.decrypt(encryptedPrivateText, password);
        const privateText = bytes.toString(CryptoJS.enc.Utf8);
        
        setDecodedData({ publicText, privateText });
      } catch (error) {
        console.error("Error decrypting the private text:", error);
      }
    }
  };

  return (
    <div>
      <p><b>Public Text</b>: {decodedData.publicText}</p>
      <p><b>Private Text</b>: {decodedData.privateText}</p>
      <QrReader
        onResult={handleScan}
        constraints={{ facingMode: 'environment' }}
        style={{ width: '100%' }}
      />
    </div>
  );
};

export default QRCodeScanner;
