import React, { useState, useRef } from 'react';
import QRCode from 'qrcode';
import CryptoJS from 'crypto-js';

const QRCodeGenerator = () => {
  const [publicText, setPublicText] = useState('');
  const [privateText, setPrivateText] = useState('');
  const [qrUrl, setQrUrl] = useState('');
  const password = "MySecretPassword4ReactQR";
  const qrRef = useRef(null); // Ref for the QR code image

  const generateQRCode = async () => {
    try {
      // Encrypt the private text
      const encryptedPrivateText = CryptoJS.AES.encrypt(privateText, password).toString();
      // Concatenate public text and encrypted private text
      const data = `${publicText}||${encryptedPrivateText}`;
      const url = await QRCode.toDataURL(data);
      setQrUrl(url);
    } catch (error) {
      console.error(error);
    }
  };

  const downloadQRCode = () => {
    // Create a link and set the URL using the `href` attribute
    const link = document.createElement('a');
    link.href = qrUrl;
    // Set the download attribute of the link to specify the filename
    link.download = 'QRCode.png';
    // Append the link to the document
    document.body.appendChild(link);
    // Trigger the click event on the link
    link.click();
    // Remove the link from the document
    document.body.removeChild(link);
  };

  return (
    <div>
      <input type="text" value={publicText} onChange={(e) => setPublicText(e.target.value)} placeholder="Enter public text" />
      <input type="text" value={privateText} onChange={(e) => setPrivateText(e.target.value)} placeholder="Enter private text" />
      <button onClick={generateQRCode}>Generate QR Code</button>
      {qrUrl && (
        <div>
          <img ref={qrRef} src={qrUrl} alt="QR Code" />
          <button onClick={downloadQRCode}>Download QR Code</button>
        </div>
      )}
    </div>
  );
};

export default QRCodeGenerator;
