import React, { useState } from 'react';
import QRCodeGenerator from './QRCodeGenerator';
import QRCodeScanner from './QRCodeScanner';

function App() {
  // State to track the current mode, starting with 'generator' as the default
  const [mode, setMode] = useState('generator');

  // Function to toggle between modes
  const toggleMode = () => {
    setMode(mode === 'generator' ? 'scanner' : 'generator');
  };

  return (
    <div className="App">
      <button class="outline" onClick={toggleMode}>Mode Change</button>
      <p></p>
      <h1>{mode === 'generator' ? 'QR Code Generator' : 'QR Code Scanner'}</h1>
      {mode === 'generator' ? <QRCodeGenerator /> : <QRCodeScanner />}
    </div>
  );
}

export default App;
